<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form :model="form">
        <el-form-item label="角色名称" :label-width="formLabelWidth">
          <el-input v-model="form.rolename" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="角色权限" :label-width="formLabelWidth">
          <el-tree
            :data="data"
            show-checkbox
            node-key="id"
            :default-checked-keys="defaultKey"
            :props="defaultProps" ref="tree"
          >
          </el-tree>
        </el-form-item>
        <el-form-item label="角色状态" :label-width="formLabelWidth">
          <el-switch
            v-model="form.status"
            active-color="blue"
            inactive-color="grey"
            :active-value="1"
            :inactive-value="2"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" v-if="info.isAdd" @click="confirm">确 定</el-button>
        <el-button type="primary" v-else @click="update">修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {mapActions,mapGetters} from 'vuex'
import {addRole,getRole,editRole} from '../../../utils/request'
import { successAlert, } from '../../../utils/alert';
export default {
  props: ["info"],
  data() {
    return {
      form: {
        rolename: "",
        menus: "",
        status: 1,

      },
      formLabelWidth: "120px",
      defaultProps: {
          children: 'children',
          label: 'title'
        },
      defaultKey:[],//默认选中
    };
  },
  computed:{
    ...mapGetters({
      "data":"menu/menuList"
    }),
  },
  methods:{
    ...mapActions({
      'requestMenuList':'menu/menuListActions',
      "requestRoleList":'role/roleListActions',
    }),
    cancel(){
      this.info.show = false;
      this.form = {
        rolename: "",
        menus: "",
        status: 1,
      };
      // 将树形结构defaultKey值设置为空
     this.defaultKey =  this.$refs.tree.setCheckedKeys([])
    },
    confirm(){
      // 处理defaultKey的值
      this.form.menus = JSON.stringify(this.$refs.tree.getCheckedKeys())
      // 发起请求
      addRole(this.form).then(res=>{
        successAlert(res.data.msg)
        this.cancel()
        this.requestRoleList()
      })
    },
    getDetail(id){
      // 获取详情
      getRole({id}).then(res=>{
        this.form = res.data.list;
        this.defaultKey = JSON.parse(this.form.menus)
        this.form.id = id;
      })

    },
    update(){
      // 处理属性结构
      this.form.menus = JSON.stringify(this.$refs.tree.getCheckedKeys())
      editRole(this.form).then(res=>{
        successAlert(res.data.msg)
        this.cancel();
        this.requestRoleList()
      })
    }
  },
  mounted(){
    this.requestMenuList();
  }
};
</script>

<style></style>



